.mdl-switch {
  position: relative;
  z-index: 1;
  vertical-align: middle;
  display: inline-block;
  box-sizing: border-box;
  width: 100%;
  height: 24px;
  margin: 0;
  padding: 0;
  overflow: visible;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.mdl-switch.is-upgraded {
  padding-left: 28px;
}

.mdl-switch__input {
  line-height: 24px;
}

.mdl-switch.is-upgraded .mdl-switch__input {
  position: absolute;
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
  opacity: 0;
  -ms-appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  border: none;
}

.mdl-switch__track {
  background: rgba(0,0,0, 0.26);
  position: absolute;
  left: 0;
  top: 5px;
  height: 14px;
  width: 36px;
  border-radius: 14px;
  cursor: pointer;
}

.mdl-switch.is-checked .mdl-switch__track {
  background: rgba(3, 169, 244, 0.5) !important;
}

.mdl-switch__track fieldset[disabled] .mdl-switch,
.mdl-switch.is-disabled .mdl-switch__track {
  background: rgba(0,0,0, 0.12);
  cursor: auto;
}

.mdl-switch__thumb {
  background: rgb(250,250,250);
  position: absolute;
  left: 0;
  top: 2px;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  transition-duration: 0.28s;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-property: left;
}

.mdl-switch.is-checked .mdl-switch__thumb {
  background: #03a9f4 !important;
  left: 16px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.14), 0 3px 3px -2px rgba(0, 0, 0, 0.2), 0 1px 8px 0 rgba(0, 0, 0, 0.12);
}

.mdl-switch__thumb fieldset[disabled] .mdl-switch,
.mdl-switch.is-disabled .mdl-switch__thumb {
  background: rgb(189,189,189);
  cursor: auto;
}

.mdl-switch__focus-helper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-4px, -4px);
  display: inline-block;
  box-sizing: border-box;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: transparent;
}

.mdl-switch.is-focused .mdl-switch__focus-helper {
  box-shadow: 0 0 0px 20px rgba(0, 0, 0, 0.1);
  background-color: rgba(0, 0, 0, 0.1);
}

.mdl-switch.is-focused.is-checked .mdl-switch__focus-helper {
  box-shadow: 0 0 0px 20px rgba(63,81,181, 0.26);
  background-color: rgba(63,81,181, 0.26);
}

.mdl-switch__label {
  position: relative;
  cursor: pointer;
  font-size: 16px;
  line-height: 24px;
  margin: 0;
  left: 24px;
}

.mdl-switch__label fieldset[disabled] .mdl-switch,
.mdl-switch.is-disabled .mdl-switch__label {
  color: rgb(189,189,189);
  cursor: auto;
}

.mdl-switch__ripple-container {
  position: absolute;
  z-index: 2;
  top: -12px;
  left: -14px;
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  cursor: pointer;
  overflow: hidden;
  -webkit-mask-image: -webkit-radial-gradient(circle, white, black);
  transition-duration: 0.40s;
  transition-timing-function: step-end;
  transition-property: left;
}

.mdl-switch__ripple-container .mdl-ripple {
  background: rgb(63,81,181);
}

.mdl-switch__ripple-container fieldset[disabled] .mdl-switch,
.mdl-switch.is-disabled .mdl-switch__ripple-container {
  cursor: auto;
}

fieldset[disabled] .mdl-switch .mdl-switch__ripple-container .mdl-ripple,
.mdl-switch.is-disabled .mdl-switch__ripple-container .mdl-ripple {
  background: transparent;
}

.mdl-switch.is-checked .mdl-switch__ripple-container {
  left: 2px;
}

.mdl-switch {
  width: auto;
}

/**
 *
 * lang-switch
 *
 */
.lang-switch {
  position: relative;
  right: -8px;
  display: inline-block;
  box-sizing: border-box;
  width: 60px;
  height: 20px;
  margin-left: auto;
  padding: 0 3px;
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.26);
}

.lang-switch__input {
  display: none;
}

.lang-switch__input:checked + .lang-switch__label {
  color: #fff;
}

.lang-switch__label {
  font-size: 10px;
  line-height: 20px;
  position: inherit;
  z-index: 10;
  float: left;
  width: 50%;
  cursor: pointer;
  transition: color 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  text-align: center;
  color: #999;
}

.lang-switch__toggle {
  font-size: 12px;
  line-height: 30px;
  position: absolute;
  top: 2px;
  left: 2px;
  float: left;
  width: 50%;
  height: 16px;
  cursor: pointer;
  transition: left 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  border-radius: 1px;
  background-color: #03a9f4;
  will-change: transform;
}

.lang-switch__input--left:checked ~ .lang-switch__toggle {
  border-radius: 10px 0 0 10px;
}

.lang-switch__input--right:checked ~ .lang-switch__toggle {
  left: 28px;
  border-radius: 0 10px 10px 0;
}

.mdl-selectfield {
  position: relative;
  font-size: 16px;
  display: inline-block;
  box-sizing: border-box;
  width: 300px;
  max-width: 100%;
  margin: 0;
  padding: 20px 0;
}

.mdl-selectfield--align-right {
  text-align: right;
}

.mdl-selectfield--full-width {
  width: 100%;
}

.mdl-selectfield__select {
  display: block;
  width: 100%;
  padding: 4px 0;
  margin: 0;
  color: inherit;
  background: transparent;
  font-size: 16px;
  text-align: left;
  border: none;
  border-bottom: 1px solid #670ce8;
  border-radius: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
}

.mdl-selectfield__select::-ms-expand {
  display: none;
}

.mdl-selectfield.is-focused .mdl-selectfield__select {
  outline: none;
}

.mdl-selectfield.is-invalid .mdl-selectfield__select {
  border-color: rgb(222, 50, 38);
  box-shadow: none;
}

fieldset[disabled] .mdl-selectfield .mdl-selectfield__select, .mdl-selectfield.is-disabled .mdl-selectfield__select {
  background-color: transparent;
  border-bottom: 1px dotted #670ce8;
  color: rgba(0,0,0, 0.26);
}

.mdl-selectfield__label {
  bottom: 0;
  color: rgba(0,0,0, 0.26);
  font-size: 16px;
  left: 0;
  right: 0;
  pointer-events: none;
  position: absolute;
  display: block;
  top: 24px;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-align: left;
}

.mdl-selectfield.is-dirty .mdl-selectfield__label {
  visibility: hidden;
}

.mdl-selectfield--floating-label .mdl-selectfield__label {
  transition-duration: 0.2s;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

fieldset[disabled] .mdl-selectfield .mdl-selectfield__label,
.mdl-selectfield.is-disabled.is-disabled .mdl-selectfield__label {
  color: rgba(0,0,0, 0.26);
}

.mdl-selectfield--floating-label.is-focused .mdl-selectfield__label,
.mdl-selectfield--floating-label.is-dirty .mdl-selectfield__label {
  color: #03a9f4;
  font-size: 12px;
  top: 4px;
  visibility: visible;
}

.mdl-selectfield--floating-label.is-invalid .mdl-selectfield__label {
  color: rgb(222, 50, 38);
  font-size: 12px;
}

.mdl-selectfield__label:after {
  background-color: #03a9f4;
  bottom: 20px;
  content: '';
  height: 2px;
  left: 45%;
  position: absolute;
  transition-duration: 0.2s;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  visibility: hidden;
  width: 10px;
}

.mdl-selectfield.is-focused .mdl-selectfield__label:after {
  left: 0;
  visibility: visible;
  width: 100%;
}

.mdl-selectfield.is-invalid .mdl-selectfield__label:after {
  background-color: rgb(222, 50, 38);
}

.mdl-selectfield__error {
  color: rgb(222, 50, 38);
  position: absolute;
  font-size: 12px;
  margin-top: 3px;
  visibility: hidden;
  display: block;
}

.mdl-selectfield.is-invalid .mdl-selectfield__error {
  visibility: visible;
}

.mdl-selectfield__icon {
  bottom: 20px;
  cursor: pointer;
  color: rgba(0,0,0, 0.26);
  padding: 4px 0;
  position: absolute;
  top: 20px;
  right: 0;
  pointer-events: none;
}

.mdl-selectfield__icon > i {
  outline: 0;
}

.mdl-selectfield.is-dirty .mdl-selectfield__icon {
  color: inherit;
}

fieldset[disabled] .mdl-selectfield .mdl-selectfield__icon,
.mdl-selectfield.is-disabled.is-disabled .mdl-selectfield__icon {
  color: rgba(0,0,0, 0.26);
}

.mdl-selectfield--floating-label.is-invalid .mdl-selectfield__icon {
  color: rgb(222, 50, 38);
}

.mdl-selectfield--mini {
  width: auto;
  min-width: 30px;
  padding: 0;
}

.mdl-selectfield--currency {
  position: absolute;
  top: 33px;
  left: 0;
}

@media (min-width: 701px) {
  .mdl-selectfield--currency {
    top: 0;
  }
}

.mdl-selectfield--full {
  width: 100%;
  padding: 0 10px 0 15px;
}

.mdl-selectfield--mini .mdl-selectfield__label::after {
  bottom: 0;
}

.mdl-selectfield--full.is-focused .mdl-selectfield__label::after {
  left: 15px;
  width: calc(100% - 25px);
  bottom: 5px;
}

.mdl-selectfield__select {
  font-size: 14px;
  padding-left: 5px;
}

.mdl-selectfield--mini .mdl-selectfield__select {
  padding-top: 2px;
  padding-bottom: 2px;
}

.mdl-selectfield--currency .mdl-selectfield__select {
  font-size: 16px;
  position: relative;
  z-index: 2;
  padding-right: 5px;
  color: rgba(0, 0, 0, 0.87);
  border-bottom-color: #5d5d5d;
}

