.fs-search .mdl-textfield {
  position: relative;
  font-size: 16px;
  display: inline-block;
  box-sizing: border-box;
  width: 300px;
  max-width: 100%;
  margin: 0;
  padding: 20px 0;
}

.fs-search .mdl-textfield .mdl-button {
  position: absolute;
  bottom: 20px;
}

.mdl-textfield--align-right {
  text-align: right;
}

.mdl-textfield--full-width {
  width: 100%;
}

.mdl-textfield--expandable {
  min-width: 32px;
  width: auto;
  min-height: 32px;
}

.mdl-textfield--expandable .mdl-button--icon {
  top: 16px;
}

.fs-search .mdl-textfield__input {
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.5);
  display: block;
  font-size: 16px;
  font-family: "Helvetica", "Arial", sans-serif;
  margin: 0;
  padding: 4px 0;
  width: 100%;
  background: none;
  text-align: left;
  color: inherit;
}

.fs-search .mdl-textfield__input[type="number"] {
  -moz-appearance: textfield;
}

.fs-search .mdl-textfield__input[type="number"]::-webkit-inner-spin-button, .fs-search .mdl-textfield__input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.mdl-textfield.is-focused .fs-search .mdl-textfield__input {
  outline: none;
}

.mdl-textfield.is-invalid .fs-search .mdl-textfield__input {
  border-color: rgb(213,0,0);
  box-shadow: none;
}

fieldset[disabled] .mdl-textfield .fs-search .mdl-textfield__input,
.mdl-textfield.is-disabled .fs-search .mdl-textfield__input {
  background-color: transparent;
  border-bottom: 1px dotted rgba(0,0,0, 0.12);
  color: rgba(0,0,0, 0.26);
}

.mdl-textfield textarea.mdl-textfield__input {
  display: block;
}

.mdl-textfield__label {
  bottom: 0;
  color: rgba(0, 0, 0, 0.5);
  font-size: 16px;
  left: 0;
  right: 0;
  pointer-events: none;
  position: absolute;
  display: block;
  top: 24px;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-align: left;
}

.mdl-textfield.is-dirty .mdl-textfield__label,
.mdl-textfield.has-placeholder .mdl-textfield__label {
  visibility: hidden;
}

.mdl-textfield--floating-label .mdl-textfield__label {
  transition-duration: 0.2s;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.mdl-textfield--floating-label.has-placeholder .mdl-textfield__label {
  transition: none;
}

fieldset[disabled] .mdl-textfield .mdl-textfield__label,
.mdl-textfield.is-disabled.is-disabled .mdl-textfield__label {
  color: rgba(0,0,0, 0.26);
}

.mdl-textfield--floating-label.is-focused .mdl-textfield__label,
.mdl-textfield--floating-label.is-dirty .mdl-textfield__label,
.mdl-textfield--floating-label.has-placeholder .mdl-textfield__label {
  color: #03a9f4;
  font-size: 12px;
  top: 4px;
  visibility: visible;
}

.mdl-textfield--floating-label.is-focused .mdl-textfield__expandable-holder .mdl-textfield__label,
.mdl-textfield--floating-label.is-dirty .mdl-textfield__expandable-holder .mdl-textfield__label,
.mdl-textfield--floating-label.has-placeholder .mdl-textfield__expandable-holder .mdl-textfield__label {
  top: -16px;
}

.mdl-textfield--floating-label.is-invalid .mdl-textfield__label {
  color: rgb(213,0,0);
  font-size: 12px;
}

.mdl-textfield__label:after {
  background-color: #03a9f4;
  bottom: 20px;
  content: '';
  height: 2px;
  left: 45%;
  position: absolute;
  transition-duration: 0.2s;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  visibility: hidden;
  width: 10px;
}

.mdl-textfield.is-focused .mdl-textfield__label:after {
  left: 0;
  visibility: visible;
  width: 100%;
}

.mdl-textfield.is-invalid .mdl-textfield__label:after {
  background-color: rgb(213,0,0);
}

.mdl-textfield__error {
  color: rgb(213,0,0);
  position: absolute;
  font-size: 12px;
  margin-top: 3px;
  visibility: hidden;
  display: block;
}

.mdl-textfield.is-invalid .mdl-textfield__error {
  visibility: visible;
}

.mdl-textfield__expandable-holder {
  display: inline-block;
  position: relative;
  margin-left: 32px;
  transition-duration: 0.2s;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  display: inline-block;
  max-width: 0.1px;
}

.mdl-textfield.is-focused .mdl-textfield__expandable-holder, .mdl-textfield.is-dirty .mdl-textfield__expandable-holder {
  max-width: 600px;
}

.mdl-textfield__expandable-holder .mdl-textfield__label:after {
  bottom: 0;
}

.fs-search.wrp-textfield {
  line-height: 1;
  position: fixed;
  z-index: 9999999999;
  top: 80px;
  left: 0;
  box-sizing: border-box;
  width: 100%;
  max-width: 480px;
  padding: 30px 46px 6px 49px;
  transition: opacity .3s ease-out;
  border-radius: 2px;
  background-color: white;
}

@media (min-width: 481px) {
  .fs-search.wrp-textfield {
    left: calc(50% - 240px);
  }
}

.fs-search .mdl-textfield {
  position: relative;
  width: calc(100% - 15px) !important;
  padding-top: 22px;
}

.fs-search .mdl-textfield--mini {
  bottom: 6px;
  padding: 0 10px 0 15px;
}

.fs-search .mdl-textfield__label {
  font-family: 'Roboto', sans-serif;
  z-index: 3;
  margin-bottom: 0 !important;
  user-select: none;
}

.fs-search .mdl-textfield__close, .fs-search .mdl-textfield__search {
  display: block;
  box-sizing: content-box;
  width: 22px;
  height: 22px;
}

.fs-search .mdl-textfield__close:active, .fs-search .mdl-textfield__search:active {
  background-color: #f2f2f2;
}

.fs-search .mdl-textfield__close.svg-icon {
  position: absolute;
  z-index: 1;
  top: -18px;
  right: -45px;
  padding: 7px;
  cursor: pointer;
  background-position: -3px -3px;
}

.fs-search .mdl-textfield__search.svg-icon {
  position: absolute;
  z-index: 1;
  right: -45px;
  bottom: 14px;
  padding: 7px;
  cursor: pointer;
  background-position: -2px -45px;
}

.fs-search .mdl-textfield__input {
  position: relative;
  z-index: 2;
  height: auto;
  margin: 0;
  outline: 0;
  border-radius: 0;
  box-shadow: none;
  background-color: white;
}

.fs-search .mdl-textfield__input--indent {
  text-indent: 20px;
}

.fs-search .mdl-textfield__input--popup {
  border-bottom-color: #670ce8;
  background-color: transparent;
}

.fs-search .mdl-textfield__favicon {
  position: absolute;
  z-index: 3;
  top: 28px;
  left: -2px;
  width: 17px;
  height: 17px;
  padding: 0;
}

.fs-search .mdl-textfield__suggests {
  font-family: 'Roboto', sans-serif;
  position: absolute;
  z-index: 1;
  top: 49px;
  left: 0;
  box-sizing: border-box;
  width: 100%;
  padding: 30px 0 20px;
  background-color: white;
  box-shadow: 0px 3px 1px rgba(0, 0, 0, 0.2);
}

.fs-search .mdl-textfield__suggests-ul {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: left;
}

.fs-search .mdl-textfield__suggests-suggest {
  font-size: 16px;
  line-height: 33px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 15px;
}

.fs-search .mdl-textfield__suggests-suggest span {
  position: relative;
  top: -1px;
  overflow: hidden;
  width: 100%;
  white-space: nowrap;
  user-select: none;
  text-overflow: ellipsis;
}

.fs-search .mdl-textfield__suggests-suggest:hover {
  background-color: #e8e8e8;
}

.fs-search .mdl-textfield__suggests-suggest.is-active {
  background-color: #e8e8e8;
}

.fs-search .mdl-textfield__suggests-suggest.is-remove {
  color: #7a7a7a;
}

.fs-search .mdl-textfield__suggests-button {
  font-size: 13px;
  padding: 0;
  cursor: pointer;
  color: #2196f3;
  border: 0;
  border-bottom: 1px solid transparent;
  background-color: transparent;
}

.fs-search .mdl-textfield__suggests-button:hover {
  border-color: #2196f3;
}

.fs-search .svg-icon {
  background: url("chrome-extension://__MSG_@@extension_id__/img/svg-out/css/svg/sprite.css-ad864b8a.svg") 0 0 no-repeat;
}

/**
 *
 * help
 *
 */
.help {
  position: absolute;
  z-index: 100;
  line-height: 1.4;
  top: 0;
  left: 0;
  overflow: auto;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding-top: 30px;
  transition: all .2s linear;
  background-color: white;
}

.help.is-hidden {
  left: -100%;
}

.help p {
  font-size: 13px;
  line-height: 1.4;
}

.help__close {
  position: absolute;
  z-index: 1;
  top: 13px;
  right: 13px;
}

.help table {
  width: 100%;
  color: #666666;
}

.help table:last-child {
  margin-bottom: 15px;
}

.help tr td:first-child {
  min-width: 135px;
}

.help tr td:last-child {
  padding-left: 10px;
}

.help td {
  font-size: 13px;
  padding: 5px 0;
}

.help caption {
  font-weight: 700;
  padding-bottom: 5px;
  text-indent: 5px;
  text-align: left;
}

.help code {
  font-size: 13px;
  box-sizing: border-box;
  display: block;
  width: 100%;
  padding: 2px 5px;
  text-align: center;
  color: #666666;
  background-color: #eeeeee;
}
